.container {
    padding: 0.2em;
    width: 50%;
}

.container.gird {
    width: 50%;
}

.container.list {
    width: 100%;
}

.video {
    
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 3px 3px 5px 0px rgba(191,191,191,0.53);
    cursor: pointer;
    transition: transform 250ms ease-in;
}

.video:hover {
    transform: scale(1.02);
}

.metadata {
    margin-left: 0.2em;
}

.thumbnail {
    border-radius: 5px;
    width: 40%;
    height: 100%;
}

.title, .channel {
    margin: 0;
    font-size: 0.8rem;
}

.channel {
    font-size: 0.6rem;
}